import { MenuPreview } from "../cmps/MenuPreview"

export const Menu = () => {

    const objKnafe = require('../data/items.json');
    const objMore = require('../data/more.json');

    return (
        <div className="menu-container">
            <p className="menu-title">  </p>
            <p className="menu-postage" style={{ fontSize: '18px' }}>  - 15 "</p>
            <p className="menu-postage" style={{ marginBottom: '40px'}}>   - 7 "*</p>
            <p className="menu-postage sale">  !!!  </p>
            <p className="menu-title knafe">:</p>
            {objKnafe.map((knafa) => <MenuPreview obj={knafa} />)}
            <p className="menu-title more">:</p>
            {objMore.map((knafa) => <MenuPreview obj={knafa} />)}
        </div>
    )
}
